.footer {
    width: 100%;
    height: 200px;
    background: #555555;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 200px;
  }
  
  .socialMedia svg {
    color: white;
    margin: 20px;
    font-size: 30px;
    cursor: pointer;
    transition: all .5s;
  }
  .socialMedia svg:hover {
    scale: 1.5;
    color: #67e95b;
  }
  
  .footer p {
    color: white;
  }
  
  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 50px;
    }
  }